.form-container {
  max-width: 400px;
  margin: 0 auto;
  overflow-x: hidden;
}

.form {
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  border: 2px solid #2196f3;
  border-radius: 10px;
  padding: 16px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #f5f8fc;
}

.form-group {
  margin-bottom: 24px;
}

.form-label {
  font-weight: bold;
  margin-bottom: 8px;
  color: #2196f3;
}

.form-input,
.form-select {
  width: 100%;
  height: 40px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  background-color: #fff;
  color: #333;
}

.form-button {
  padding: 10px 20px;
  border-radius: 4px;
  background-color: #2196f3;
  color: #fff;
  border: none;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.form-button:hover {
  background-color: #1976d2;
}

.error-message {
  display: flex;
  justify-content: center;
  color: red;
  font-size: 14px;
  margin-top: 4px;
}

@media (max-width: 480px) {
  .form-input,
  .form-select,
  .form-button {
    font-size: 16px;
    width: 100%;

    .form-container {
      max-width: 100%;
      padding: 0 16px;
    }
  }
}
